import React, {useEffect, useState} from 'react';
import './App.css';
import { PrimaryButton, MessageBar } from '@fluentui/react'
import { useId } from '@fluentui/react-hooks';
import { TextField } from '@fluentui/react/lib/TextField';
import { Stack } from '@fluentui/react/lib/Stack';
import { Separator } from '@fluentui/react/lib/Separator';
import { Label } from '@fluentui/react/lib/Label';
import * as wasm from 'wasm-similar';

const stackStyles = { root: { width: 650 } };
const stackTokens = { childrenGap: 50 };
const columnProps = {
  tokens: { childrenGap: 15 },
  styles: { root: { width: 300 } },
};

const style = {
  background: '#ffffff',
  padding: '28px',
  marginBottom: '28px',
  borderRadius: 2,
  backgroundClip: 'padding-box',
  boxShadow: '0 1.6px 3.6px 0 rgb(0 0 0 / 13%), 0 0.3px 0.9px 0 rgb(0 0 0 / 11%)',
  width: '800px',
  height: '400px',
  textAlign: 'left'
}

function App() {
  const tooltipId = useId('tooltip');
  const [source, setSource] = useState('');
  const [target, setTarget] = useState('');
  const [result, setResult] = useState();
  useEffect(() => {
  }, []);

  const red = {
    background: '#fff2f0',
    color: 'black',
    textDecoration:"line-through"
  }

  const green = {
    background: '#f6ffed',
    color: 'black',
  }

  const black = {
    background: '#fff',
    color: 'black',
  }

  const compareHandler = () => {
    const list = wasm.compare_from_chars(source, target);
    
    const elements = list.map((item, index) => {
      if (item.op === ' ') {
        return <span key={index} style={black}>{item.text}</span>;
      }
      if (item.op === '+') {
        return <span key={index} style={green}>{item.text}</span>;
      }
      if (item.op === '-') {
        return <span key={index} style={red}>{item.text}</span>;
      }
      return ''
    });
    setResult(elements)
  }
  return (
    <div className="App">
      <header className="App-header">
      
      <Stack horizontal tokens={stackTokens} styles={stackStyles}>
      <MessageBar>
        This is a simple demo shows how this wasm-similar works.
        wasm-similar
      </MessageBar>
        </Stack>
        <Stack horizontal tokens={stackTokens} styles={stackStyles}>
          
          <Stack {...columnProps}>
            <TextField label="Source Text " multiline autoAdjustHeight value={source} onChange={(e, newValue) => setSource(newValue)} />
          </Stack>
          <Stack {...columnProps}>
            <TextField label="Target Text " multiline autoAdjustHeight value={target} onChange={(e, newValue) => setTarget(newValue)} />
          </Stack>
        </Stack>
        <Separator></Separator>

        <PrimaryButton aria-describedby={tooltipId} text="Compare " onClick={compareHandler} />
        
        <Separator></Separator>
        <Label>Diff result</Label>
        <div style={style}>
          {result}
        </div>
      </header>
    </div>
  );
}

export default App;
